.subscription-plans-list{
    margin-bottom: 30px;
}
.subscription-plan.card{
    border:0;
    border-radius: 4px;
    overflow: hidden;
}
.subscription-plan .card-title{
    margin: 0;
}
.subscription-plan .card-title h4{
    font-size: 24px;
    font-weight: 600;
    color: #fff;
    height: 60px;
    line-height: 60px;
    background-color: #2F80ED;
    padding: 0 20px;    
}

.subscription-plan .card-body{
    background-image: url('../../images/backgrounds/subscription-bg.svg');
    background-repeat: repeat-y;
    background-size: cover;
}

.plan-fetures-list li{
    font-size: 16px;
    color: #6E7775;
    padding-bottom: 15px;
    position: relative;
}

.plan-fetures-list li::before{
    margin-right: 10px;
    font-size: 12px;
}

.plan-price{
    font-size: 24px;
    font-weight: 600;
    color: #2F80ED;
    padding-bottom: 20px;
}
.plan-price span{
    font-size: 13px;
    color: #B0B0B0;
}

.see-more-btn{
    background-color: transparent;
    border:0;
    font-size: 16px;
    color: #2F80ED;
}
.subscription-history ul{
    margin: -20px 0;
}
.subscription-history ul li{
    position: relative;
    padding: 20px 0;
    border-bottom: 1px solid #ECECEC;
}
.subscription-history ul li:last-child{
    border-bottom-color: transparent;
}
.subscription-history ul li .plan-name,
.subscription-history ul li .plan-cost{
    display: block;
    font-size: 24px;
    font-weight: 600;
    color: #160102;
}

.subscription-history ul li .plan-name .active{
    font-size: 14px;
    font-weight: 400;
    line-height: 24px;
    color: #fff;
    background-color: #219653;
    border-radius: 4px;
    padding: 0 10px;
    margin-left: 10px;
}

.subscription-history ul li .plan-duration{
    display: block;
    font-size: 16px;
    color: #6E7775;
    padding-top: 5px;
}
.subscription-history ul li .plan-cost{
    position: absolute;
    right: 0;
    top: 20px;
}

@media screen and (max-width: 991px){
    
    .subscription-plans-list > [class*="col-"]{
        padding-bottom: 30px;
    }
    .subscription-plans-list > [class*="col-"]:last-child{
        padding-bottom: 0;
    }
}

@media screen and (max-width: 575px){
    .plan-price{
        font-size: 20px;
    }
    .subscription-plan .card-title h4{
        font-size: 22px;
        height: 50px;
        line-height: 50px;
    }
    .subscription-history ul li .plan-name,
    .subscription-history ul li .plan-cost{
        font-size: 18px;
    }
    .subscription-history ul li .plan-cost{
        position: static;
        margin-top: 10px;
    }
}